.Wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin-bottom: 1rem !important;
    font-size: .875rem;
    line-height: 1.5;
}

*, ::after, ::before {
    box-sizing: border-box;
}

.Title {
    width: 150px;
    background-color: var(--title-bg-color);
    padding: .25rem .5rem;
    border-radius: .2rem;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 1px solid var(--border-color); 
    text-align: left;
    font-weight: bold;
    display: flex;
    align-items: center;
    color: var(--title-color);
}

.Input {
    width: 100%;
    border-radius: .2rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    padding: .25rem .5rem;
    position: relative;
    flex: 1 1 0%;
    border: 1px solid var(--border-color);
    color: var(--title-color);
    display: block;
}


.InputCheckbox {
    width: 100%;
    border-radius: .2rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border: 1px solid var(--border-color);
    color: var(--title-color);
}

.Checkbox {
    width: 23px;
    height: 23px;
}