.Wrapper {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    padding: 15px;
    border-radius: 1rem;
    border-top-left-radius: 0;
}

.Button {
    color: #ffffff;
    background-color: var(--button-color);;
    border-color: var(--button-color);;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.Button:hover {
    background-color: var(--button-color-hover);
}


@media (max-width: 550px) {
    .Button {
        margin-left: 5px;
        margin-top: 5px;
        font-size: .65rem;
    }
}

.Ability {
    background-color: black;
    border-radius: 50%;
    border: 3px solid var(--border-color);
    width: var(--ability-width);
    height: var(--ability-width);
    display: flex;
    align-items: center;
}

.DrinkSlots {
    width: 100%;
    border-radius: .2rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    padding: .25rem .5rem;
    position: relative;
    flex: 1 1 0%;
    border: 1px solid var(--border-color);
    color: #495057;
    display: block;
    display: flex;
    align-items: center;
    gap: 5px;
    --ability-width: 32px;
}

.Gap {
    margin: 5px;
}

.Title {
    width: 150px;
    background-color: var(--title-bg-color);
    padding: .25rem .5rem;
    border-radius: .2rem;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 1px solid var(--border-color);
    text-align: left;
    font-weight: bold;
    display: flex;
    align-items: center;
}

.InputWrapper {
    width: 100%;
    display: flex;
    align-items: stretch;
    margin-bottom: 1rem !important;
    font-size: .875rem;
    line-height: 1.5;
}

.Input {
    width: 100%;
    border-radius: .2rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    padding: .25rem .5rem;
    position: relative;
    flex: 1 1 0%;
    border: 1px solid var(--border-color);
    color: var(--title-color);
    display: block;
    display: flex;
    align-items: center;
    gap: 5px;
    --ability-width: 32px;
}

.abilitiyList {
    --ability-width: 32px;
    display: flex;
    gap: 4px;
    flex-flow: row wrap;
    padding: 5px;
}

.Entry {
    display: flex;
    align-items: center;
}
