.Wrapper {
    box-sizing: border-box;
    display: flex;
    flex-flow: row wrap;
    margin-top: -8px;
    margin-left: -8px;
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    border-radius: 1rem;
}

.Card {
    padding-left: 8px;
    padding-top: 8px;
    min-width: 210px;
}

.Subtitle {
    padding: 8px 16px;
}

.Subtitleh6 {
    margin: 0px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.75;
    letter-spacing: 0.00938em;
}

.Divider {
    margin: 0px;
    flex-shrink: 0;
    border-width: 0px 0px thin;
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.12);
}

.cardWrapperWrapper {
    padding: 16px;
    display: flex;
    gap: 16px;
    flex-direction: column;
    width: 100%;
}

.cardWrapper {
    color: rgba(255, 255, 255, 0.9);
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 14px;
    box-shadow: none;
    background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0));
    overflow: hidden;
    background-color: rgb(42, 54, 77);
    width: 100%;
}

.CardDark {
    color: rgba(255, 255, 255, 0.9);
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 4px;
    box-shadow: none;
    overflow: hidden;
    background-color: rgb(72, 107, 172);
    height: 100%;
    width: 100%;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    display: flex;
    padding: 16px;
}

.GridStyle {
    box-sizing: border-box;
    display: flex;
    flex-flow: row wrap;
    margin-top: -8px;
    width: calc(100% + 8px);
    margin-left: -8px;
}

.GridElem {
    padding-left: 8px;
    padding-top: 8px;
    box-sizing: border-box;
    margin: 0px;
    flex-direction: row;
    flex-basis: 50%;
    flex-grow: 0;
    max-width: 50%;
}

.TextDescription {
    margin: 0px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.CardLight {
    color: rgba(255, 255, 255, 0.9);
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 4px;
    box-shadow: none;
    overflow: hidden;
    background-color: rgb(72, 107, 172);
    height: 100%;
    width: 100%;
    min-height: 300px;
    display: flex;
    flex-direction: column;
}

.CardContent {
    padding: 16px;
    color: rgba(255, 255, 255, 0.9);
    display: flex;
    flex-direction: column;
    gap: 2px;
    max-width: 100%;
}

.CardBox {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.TopRow {
    display: flex;
    align-items: center;
    flex-direction: column-reverse;
    justify-content: space-evenly;
    width: 100%;
}

.Gear {
    width: 82px;
    height: 82px;
}

.Brand {
    height: 48px;
}

.abilityImage {
    width: var(--ability-with);
    height: var(--ability-with);
}

.Main {
    background-color: black;
    border-radius: 50%;
    --ability-with: 46px;
    width: var(--ability-with);
    height: var(--ability-with);
    margin: 0 auto;
}

.Subs {
    --ability-with: 32px;
    display: flex;
    justify-content: center;
    gap: 5px;
    margin-top: 10px;
}

.SubAbility {
    background-color: black;
    border-radius: 50%;
    width: var(--ability-with);
    height: var(--ability-with);
}

.Button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    box-sizing: border-box;
    outline: currentcolor none 0px;
    border: 0px none;
    margin: 0px;
    cursor: pointer;
    user-select: none;
    vertical-align: middle;
    appearance: none;
    text-decoration: none;
    text-transform: none;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.75;
    letter-spacing: 0.02857em;
    min-width: 64px;
    padding: 6px 16px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    color: rgb(255, 255, 255);
    background-color: rgb(23, 162, 184);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 1px -2px, rgba(0, 0, 0, 0.14) 0px 2px 2px 0px, rgba(0, 0, 0, 0.12) 0px 1px 5px 0px;
    border-radius: 1em;
}

.ButtonH1 {
    margin: 0px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 300;
    font-size: 2rem;
    line-height: 1.167;
    letter-spacing: -0.01562em;
}


*, ::before, ::after {
box-sizing: inherit;
}

.SettingsContent {
    background-color: white;
    border: 1px solid #dee2e6;
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
    margin-left: 10px;
    width: 95%;
}

.ButtonInfo {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 0px none;
    cursor: pointer;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.75;
    letter-spacing: 0.02857em;
    min-width: 64px;
    padding: 6px 16px;
    border-radius: 4px;
    color: rgb(255, 255, 255);
    background-color: rgb(23, 162, 184);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 1px -2px, rgba(0, 0, 0, 0.14) 0px 2px 2px 0px, rgba(0, 0, 0, 0.12) 0px 1px 5px 0px;
    width: 100%;
}

.ButtonInfo:hover {
    text-decoration: none;
    background-color: rgb(16, 113, 128);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 4px -1px, rgba(0, 0, 0, 0.14) 0px 4px 5px 0px, rgba(0, 0, 0, 0.12) 0px 1px 10px 0px;
}

.ButtonDefault {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 0px none;
    cursor: pointer;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.75;
    letter-spacing: 0.02857em;
    min-width: 64px;
    padding: 6px 16px;
    border-radius: 4px;
    color: rgb(255, 255, 255);
    background-color: rgb(30, 120, 200);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 1px -2px, rgba(0, 0, 0, 0.14) 0px 2px 2px 0px, rgba(0, 0, 0, 0.12) 0px 1px 5px 0px;
    width: 100%;
}

.ButtonDefault:disabled {
    background:  #fff;
    border-color: #007bff;
    background-color: #025bb9;
    color: #ccc;
    cursor: no-drop;
}

.ButtonDefault:hover {
    text-decoration: none;
    background-color: rgb(21, 84, 140);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 4px -1px, rgba(0, 0, 0, 0.14) 0px 4px 5px 0px, rgba(0, 0, 0, 0.12) 0px 1px 10px 0px;
}

.ButtonError {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 0px none;
    cursor: pointer;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.75;
    letter-spacing: 0.02857em;
    min-width: 64px;
    padding: 6px 16px;
    border-radius: 4px;
    color: rgb(255, 255, 255);
    background-color: rgb(200, 60, 60);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 1px -2px, rgba(0, 0, 0, 0.14) 0px 2px 2px 0px, rgba(0, 0, 0, 0.12) 0px 1px 5px 0px;
    width: 100%;
}

.ButtonError:hover {
    text-decoration: none;
    background-color: rgb(140, 42, 42);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 4px -1px, rgba(0, 0, 0, 0.14) 0px 4px 5px 0px, rgba(0, 0, 0, 0.12) 0px 1px 10px 0px;
}

.ButtonDisabled {
    color: rgba(255, 255, 255, 0.3);
    box-shadow: none;
    background-color: rgba(255, 255, 255, 0.12);
    pointer-events: none;
    cursor: default;

    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 0px none;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.75;
    letter-spacing: 0.02857em;
    min-width: 64px;
    padding: 6px 16px;
    border-radius: 4px;
    width: 100%;
}

.TextTiny {
    margin: 0.35em 0px 0.35em;
    line-height: 1.66;
    letter-spacing: 0.03333em;
}