.Select {
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    color: #495057;
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    border-radius: .25rem;
    margin: 0;
    display: block;
    font-size: 1rem;
}

.SettingsContent {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
}

.Ability {
    background-color: black;
    border-radius: 50%;
    border: 3px solid var(--border-color);
}

.Brand {
    background-color: white;
    border-radius: 50%;
    border: 3px solid var(--border-color);
}

.Gap {
    margin: 5px;
}