:root {
  --background: #e9ecef;
  --text-primary: black;
  --text-secondary: royalblue;
  --border-color: #dee2e6;
  --wrapper-background: white;
  --title-color: #495057;
  --title-bg-color: #e9ecef; 
  --inactive-bg-color: #eeeeee;
  --even-bg: rgba(0,0,0,.05);
  --odd-bg: #dee2e6;
  --table-color: #212529;
  --option-bg: hsl(0, 0%, 90%);
  --option-selected-bg-active: hsl(0 0,%, 80%);
  --option-selected-bg: hsl(0, 0%, 70%);
  --button-color: #007bff;
  --button-color-hover:#6cb3ff;
}

[data-theme='dark'] {
  color-scheme: dark;
  accent-color: var(--native-dark-accent-color);
  --background: #292929;
  --text-primary: #f1f1f1;
  --border-color: #555555;
  --wrapper-background: black;
  --title-color: #b6afa9;
  --title-bg-color: #161311; 
  --inactive-bg-color: #111111;
  --even-bg: rgba(0,0,0,.05);
  --odd-bg: #211d19;
  --table-color: #dedad6;
  --option-bg: hsl(0, 0%, 10%);
  --option-selected-bg-active: hsl(0, 0%, 20%);
  --option-selected-bg: hsl(0, 0%, 30%);
  --button-color: hsl(204, 88%, 37%);
  --button-color-hover: hsl(211, 100%, 19%);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  color: var(--text-primary);
  background-color: var(--background);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
