.NameplateEntry {
    align-items: center;
    box-sizing: border-box;
    display: flex;
    height: 35px;
    line-height: 1;
    padding: 5px;
    width: 240px;
    margin: 2px;

}

.NameplateFullEntry {
    padding: 5px;
    margin: 1px;
    border: 1px solid var(--border-color);
    border-radius: 5px;
    text-align: center;
}

.NameplateFullEntryHide {
    padding: 5px;
    width: 250px;
    margin: 1px;
    overflow: hidden;
}

@media (max-width: 900px) {
    .BannerImage {
        width: 196px;
    }
}
