.Wrapper {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
    overflow: scroll;
}

.Table {
    border: 1px solid var(--border-color);
    border-collapse: collapse;
    width: 100%;
    color: var(--table-color);
    text-align: left;
}

.TableHead {
    padding: .75rem;
    border: 1px solid var(--border-color);
}

.ContentNotFound {
    text-align: center;
    background-color: rgba(0,0,0,.05);
}

.TDEven {
    background-color: var(--even-bg);
}

.TDOdd {
    background-color: var(--odd-bg);
}