.BynameEntry {
    align-items: center;
    box-sizing: border-box;
    display: flex;
    height: 35px;
    line-height: 1;
    padding: 5px;
    width: 240px;
    border: 1px solid var(--border-color);
    margin: 2px;

}

.ByamlFullEntry {
    border: 1px solid var(--border-color);
    border-radius: 10px;
    padding: 5px;
    width: 250px;
    margin: 1px;
}
