.Content {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
}

.WrapperTop {
  background-color: var(--wrapper-background);
  border: 1px solid var(--border-color);
  padding: 15px;
  border-radius: 1rem;
  border-top-left-radius: 0;
}

.Content code {
    padding: .2em .4em;
    margin: 0;
    font-size: 85%;
    background-color: rgba(110, 118, 129, 0.4);
    border-radius: 6px;
  }

  .Content img {
    padding: 1rem
  }

  .ListElems {
    
  }

  .ListElems a:link {
    color: var(--text-primary);
    text-decoration: none;
  }
  
  .ListElems a:visited {
    color: var(--text-primary);
    text-decoration: none;
  }
  
  .ListElems a:hover {
    text-decoration: underline;
  }
  
  .ListElems a:active {
    text-decoration: underline;
  }

  .ListElems li{
    font-size:20px;
   }