.Wrapper {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
    overflow: scroll;
}

.WrapperNoScroll {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
}

.Table {
    border: 1px solid var(--border-color);
    border-collapse: collapse;
    width: 100%;
    color: var(--table-color);
    text-align: left;
}

.TableHead {
    padding: .75rem;
    border: 1px solid var(--border-color);
}

.ContentNotFound {
    text-align: center;
    background-color: rgba(0,0,0,.05);
}

.TDEven {
    background-color: var(--even-bg)
}

.TDOdd {
    background-color: var(--odd-bg)
}

.Button {
    color: #ffffff;
    background-color: var(--button-color);
    border-color: var(--button-color);
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.Button:hover {
    background-color: var(--button-color-hover);
}

.Button:disabled{
    background:  #fff;
    border-color: #007bff;
    background-color: #007bff;
    color: #ccc;
    cursor: no-drop;
 }


@media (max-width: 550px) {
    .Button {
        margin-left: 5px;
        margin-top: 5px;
        font-size: .65rem;
    }
}

.Ability {
    background-color: black;
    border-radius: 50%;
    border: 3px solid var(--border-color);
}

.ButtonArea {
    margin-top: 10px;
}

.Entry {
    display: flex;
    gap: 50px;
    align-items: center;
}

.PullEntry {
    width: 98%;
    border: 1px solid var(--border-color);
    padding: 10px;
    border-radius: 10px;
    margin-top: 10px;
}

.PullHeader{
    font-size: large;
    font-weight: bold;
    margin-bottom: 5px;
}

.Gap {
    padding: 10px;
}