.Wrapper {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
}

.BadgeInfo {
    display: grid;
    grid-template-rows: auto;
    grid-template-areas: 
      "badgeImage badgeName"
      "badgeProgress badgeProgress";
    gap: 5px;
    border: 1px solid var(--border-color);
    border-radius: 10px;
    margin: 5px;
    padding: 5px;
    width: 100%;
}

.UpdateInfo {
    display: grid;
    grid-template-rows: auto;
    grid-template-areas: 
      "updateImage updateName updateName updateName"
      "updateImage updateProgressDecrease updateProgress updateProgressIncrease";
    gap: 5px;
    border: 1px solid var(--border-color);
    border-radius: 10px;
    margin: 5px;
    padding: 5px;
    width: 100%;
}

@media (min-width: 900px) {
    .BadgeInfo {
        width: 47%;
    }
    .UpdateInfo {
        width: 47%;
    }
}
@media (min-width: 1000px) {
    .BadgeInfo {
        width: 30%;
    }
}
@media (min-width: 1200px) {
    .BadgeInfo {
        width: 23%;
    }
    .UpdateInfo {
        width: 30%;
    }
}
@media (min-width: 1550px) {
    .BadgeInfo {
        width: 15%;
    }
    .UpdateInfo {
        width: 23%;
    }
}
@media (min-width: 2350px) {
    .UpdateInfo {
        width: 15%;
    }
}
.BadgeDesc {
    grid-area: badgeName;
    font-size: larger;
    align-self: center;
}

.UpdateDesc {
    grid-area: updateName;
    font-size: larger;
    align-self: center;
    text-align: center;
}

.UpdateProgressDecrease {
    grid-area: updateProgressDecrease;
    padding: 5px;
    text-align: right;
    align-self: center;
}

.UpdateProgressIncrease {
    grid-area: updateProgressIncrease;
    padding: 5px;
    text-align: left;
    align-self: center;

}

.UpdateProgress {
    grid-area: updateProgress;
    padding: 5px;
    text-align: center;
    align-self: center;

}

.UpdateImage {
    grid-area: updateImage;
    text-align: center;
}

.BadgeImage {
    grid-area: badgeImage;
}

.BadgeProgress {
    grid-area: badgeProgress;
    padding: 5px;
}

.BadgeList {
    display: flex;
    flex-wrap: wrap;
}

.BynameList {
    display: flex;
    flex-wrap: wrap;
}


.arrow {
    cursor:pointer;
    font-size: 1rem;
}
  
.ProfileFlex {
    display:flex;
    flex-wrap: wrap;
}

.CatalogueElem {
    border: 1px solid var(--border-color);
    width: 265px;
    border-radius: 10px;
    padding: 5px;
    margin: 5px;
}

.ProfileLevel {
    margin-bottom: 5px;
    font-weight: bold;
    text-align: center;
}


.Button {
    color: #ffffff;
    background-color: var(--button-color);
    border-color: var(--button-color);
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.Button:hover {
    background-color: var(--button-color-hover);
}

.Button:disabled{
    background:  #fff;
    border-color: #007bff;
    background-color: #007bff;
    color: #ccc;
    cursor: no-drop;
 }

 
.GridStyle {
    box-sizing: border-box;
    display: flex;
    flex-flow: row wrap;
    width: calc(100% + 8px);
    margin-left: -8px;
}

.GridElem {
    padding-left: 8px;
    padding-top: 8px;
    box-sizing: border-box;
    margin: 0px;
    flex-direction: row;
    flex-basis: 50%;
    flex-grow: 0;
    max-width: 50%;
}


.CardContent {
    padding: 16px;
    color: rgba(255, 255, 255, 0.9);
    display: flex;
    flex-direction: column;
    gap: 2px;
    max-width: 100%;
}


.Divider {
    margin: 0px;
    flex-shrink: 0;
    border-width: 0px 0px thin;
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.12);
}

.TextTiny {
    margin: 0.35em 0px 0.35em;
    line-height: 1.66;
    letter-spacing: 0.03333em;
}

@media (max-width: 550px) {
    .Button {
        margin-left: 5px;
        margin-top: 5px;
        font-size: .65rem;
    }
}
@media (max-width: 600px) {
    .GridElem {
        flex-basis: 25%;
        max-width: 50%;
    }
}

