.Wrapper {
    box-sizing: border-box;
    display: flex;
    flex-flow: row wrap;
    margin-top: -8px;
    margin-left: -8px;
}

.Card {
    padding-left: 8px;
    padding-top: 8px;
    min-width: 210px;
}

.CardDark {
    color: rgba(255, 255, 255, 0.9);
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 4px;
    box-shadow: none;
    overflow: hidden;
    background-color: rgb(72, 107, 172);
    height: 100%;
    width: 100%;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    --brand-width: 48px;
    --gear-width:  82px;
}

.CardContent {
    padding: 16px;
    color: rgba(255, 255, 255, 0.9);
    text-align: center;
}

.CardBox {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.TopRow {
    display: flex;
    align-items: center;
    flex-direction: column-reverse;
    justify-content: space-evenly;
    width: 100%;
}

.Gear {
    width: var(--gear-width);
    height: var(--gear-width);
}

.Brand {
    width: var(--brand-width);
    height: var(--brand-width);

}

.abilityImage {
    width: var(--ability-width);
    height: var(--ability-width);
}

.Main {
    background-color: black;
    border-radius: 50%;
    --ability-width: 46px;
    border: 3px solid var(--border-color);
    width: var(--ability-width);
    height: var(--ability-width);
    margin: 0 auto;
}

.MainAbility {
    background-color: black;
    border-radius: 50%;
    --ability-width: 46px;
    border: 3px solid var(--border-color);
    width: var(--ability-width);
    height: var(--ability-width);
}

.Subs {
    --ability-width: 32px;
    display: flex;
    justify-content: center;
    gap: 5px;
    margin-top: 10px;
}

.SubAbility {
    background-color: black;
    border-radius: 50%;
    width: var(--ability-width);
    height: var(--ability-width);
}

.Button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    box-sizing: border-box;
    outline: currentcolor none 0px;
    border: 0px none;
    margin: 0px;
    cursor: pointer;
    user-select: none;
    vertical-align: middle;
    appearance: none;
    text-decoration: none;
    text-transform: none;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.75;
    letter-spacing: 0.02857em;
    min-width: 64px;
    padding: 6px 16px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    color: rgb(255, 255, 255);
    background-color: rgb(23, 162, 184);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 1px -2px, rgba(0, 0, 0, 0.14) 0px 2px 2px 0px, rgba(0, 0, 0, 0.12) 0px 1px 5px 0px;
    border-radius: 1em;
}


.ButtonSimple {
    color: #ffffff;
    background-color: #007bff;
    border-color: #007bff;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.ButtonSimple:hover {
    background-color: #6cb3ff;
}

.ButtonH1 {
    margin: 0px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 300;
    font-size: 2rem;
    line-height: 1.167;
    letter-spacing: -0.01562em;
}


*, ::before, ::after {
box-sizing: inherit;
}

.FlexContent {
    display: flex;
    align-items: center;
    gap: 5px
}

.SettingsContent {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
    margin-left: 10px;
}


.Table {
    border: 1px solid var(--border-color);
    border-collapse: collapse;
    width: 100%;
    color: var(--table-color);
    text-align: left;
    --brand-width: 48px;
    --gear-width:  64px;
}

.TableHead {
    padding: .75rem;
    border: 1px solid var(--border-color);
}

.ContentNotFound {
    text-align: center;
    background-color: rgba(0,0,0,.05);
}

.TDEven {
    background-color: var(--even-bg);
}

.TDOdd {
    background-color: var(--odd-bg);
}

.displayText {

}

.HoverPointer {
    cursor: pointer;
}


.likebutton {
    cursor: pointer;
    background: rgba(255, 192, 200, 0);
    border-radius: 50%;
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 100ms ease;
}

.likebutton:hover {
    background: rgba(255, 192, 203, 0.589);
}

.liked {
    cursor: pointer;
    color: rgb(255, 100, 100);
    background: rgba(255, 192, 200, 0);
    border-radius: 50%;
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 100ms ease;
}

.likedColor {
    color: rgb(255, 100, 100);
}

.liked:hover {
    background: rgba(255, 192, 203, 0.589);
}


.MultiSelect {
    width: 100%;
    border-radius: .2rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    padding: .25rem .5rem;
    position: relative;
    flex: 1 1 0%;
    border: 1px solid var(--border-color);
    color: #495057;
    display: block;
    display: flex;
    align-items: center;
    gap: 5px;
    --ability-width: 32px;
}


.Title {
    width: 150px;
    background-color: var(--title-bg-color);
    padding: .25rem .5rem;
    border-radius: .2rem;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 1px solid var(--border-color); 
    text-align: left;
    font-weight: bold;
    display: flex;
    align-items: center;
}

.InputWrapper {
    width: 100%;
    display: flex;
    align-items: stretch;
    margin-bottom: 1rem !important;
    font-size: .875rem;
    line-height: 1.5;
    --brand-width: 32px;
}

.Gap {
    margin: 5px;
}

.Entry {
    display: flex;
    align-items: center;
}

.Ability {
    background-color: black;
    border-radius: 50%;
    border: 3px solid var(--border-color);
    width: var(--ability-width);
    height: var(--ability-width);
    display: flex;
    align-items: center;
}

@media (max-width: 800px) {
    .CardDark {
        --brand-width: 32px;
        --gear-width: 64px;
        font-size: smaller;
    }
    .Main {
        --ability-width: 32px;
    }

    .MainAbility{
        --ability-width: 32px;
    }

    .SubAbility {
        --ability-width: 24px;
    }

    .Card {
        padding-left: 8px;
        padding-top: 8px;
        min-width: 160px;
    }

    .Table {
        --brand-width: 32px;
        --gear-width:  32px;
        font-size: smaller;
        text-align: center;
    }

    .TableHead {
        font-size: smaller;
    }

    .displayText {
        display: none;
    }

    .FlexContent {
        justify-content: center;
    }

    .Title {
        width: auto;
    }
    
}