.Wrapper {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-background);
    padding: 15px;
    border-radius: 1rem;
    border-top-left-radius: 0;
}

.WrapperRound {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-background);
    padding: 15px;
    border-radius: 1rem;
}

.Button {
    color: #ffffff;
    background-color: var(--button-color);
    border-color: var(--button-color);
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.Button:hover {
    background-color: var(--button-color-hover);
}


@media (max-width: 550px) {
    .Button {
        margin-left: 5px;
        margin-top: 5px;
        font-size: .65rem;
    }
}

.container {
    position: relative;
    transform: scale(0.1);
    min-height: 64px;
    bottom: 15px;
}

.upper {
    z-index: 1;
    left: 0;
    position: absolute;
}

.lower {
    top: 80px;
    left: 0;
    position: absolute;
}

.ImgRes img {
    max-height: 128px;
}

.Chunk {
    background-color: black;
    border-radius: 100px;
    border: 3px solid var(--border-color);
}

.Limited {
    border: 3px solid gold;
    background-color: gold;
    color: black;
}

.SubLimited {
    border: 3px solid silver;
    background-color: silver;
    color: black;
}

.FlexElem {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 7px;
    height: 128px;
}

.FlexElem2 {
    display: flex;
    align-items: center;
    gap: 7px;
    height: 128px;
}

.InputWrapper {
    width: 100%;
    display: flex;
    align-items: stretch;
    margin-bottom: 1rem !important;
    font-size: .875rem;
    line-height: 1.5;
}

.Input {
    width: 100%;
    border-radius: .2rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    padding: .25rem .5rem;
    position: relative;
    flex: 1 1 0%;
    border: 1px solid var(--border-background);
    color: var(--title-color);

    display: flex;
    align-items: center;
    gap: 5px;
    --ability-width: 32px;
}

.abilitiyList {
    --ability-width: 32px;
    display: flex;
    gap: 4px;
    flex-flow: row wrap;
    padding: 5px;
    text-align: center;
}

.Entry {
    display: flex;
    align-items: center;
}


.DrinkSlots {
    width: 100%;
    border-radius: .2rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    padding: .25rem .5rem;
    position: relative;
    flex: 1 1 0%;
    border: 1px solid var(--border-color);
    color: var(--title-color);
    display: flex;
    align-items: center;
    gap: 5px;
    --ability-width: 32px;
}

.Gap {
    margin: 5px;
}


.Title {
    width: 150px;
    background-color: var(--title-bg-color);
    padding: .25rem .5rem;
    border-radius: .2rem;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 1px solid var(--border-color); 
    text-align: left;
    font-weight: bold;
    display: flex;
    align-items: center;
    color: var(--title-color);
}


.ButtonError {
    color: #ffffff;
    background-color: #007bff;
    border-color: #007bff;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
    background-color: rgb(200, 60, 60);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 1px -2px, rgba(0, 0, 0, 0.14) 0px 2px 2px 0px, rgba(0, 0, 0, 0.12) 0px 1px 5px 0px;
}

.ButtonError:hover {
    text-decoration: none;
    background-color: rgb(140, 42, 42);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 4px -1px, rgba(0, 0, 0, 0.14) 0px 4px 5px 0px, rgba(0, 0, 0, 0.12) 0px 1px 10px 0px;
}

.Ability {
    background-color: black;
    border-radius: 50%;
    border: 3px solid var(--border-color);
}

.Brand {
    background-color: white;
    border-radius: 50%;
    border: 3px solid var(--border-color);
}


.calendar {
    background: var(--wrapper-background) !important;
    margin-bottom: 10px;
    margin-top: 10px;
}

.calendartile {
    background-color: var(--calendar-tile) !important;
    color: var(--color)
}

.GearSlot {
    border: 1px solid var(--border-color);
}

.GearImage {
    width: 196px;
}

.GearBrand{
    border: 1px solid var(--border-color);
    border-radius: 20px;
    justify-self: center;
    background: var(--border-color);
}

