.WrapperContent {
    background-color: var(--wrapper-background);
    border: 1px solid var(--border-color);
    padding: 15px;
    border-radius: 1rem;
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
}


.Button {
    color: #ffffff;
    background-color: var(--button-color);
    border-color: var(--button-color);
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.Button:hover {
    background-color: var(--button-color-hover);
}


@media (max-width: 550px) {
    .Button {
        margin-left: 5px;
        margin-top: 5px;
        font-size: .65rem;
    }
}

.ButtonArea {
    margin-top: 10px;
    margin-bottom: 20px;
    margin-left: 10px;
}

.FlexWrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}

.Button:disabled{
    background:  #fff;
    border-color: var(--button-color);
    background-color: var(--button-color);
    color: #ccc;
    cursor: no-drop;
 }

 .break {
    flex-basis: 100%;
    height: 0;
  }